import { Card } from '@mui/material';
import { useEffect, useState } from 'react';
import http from 'src/http';
import { IDevolution } from 'src/models/product';
import DevolutionTable from './DevolutionTable';
import React from 'react';
import { UserContext } from 'src/context/UserContext';


function Devolution() {
  const [devolution, setDevolution] = useState<IDevolution[]>([]);

  const {
    usuario
  } = React.useContext(UserContext);


  async function getAllDevolution(){
    if(usuario?.id_perfil === 1 || usuario?.id_perfil === 3){
      const {data} = await http.get(`/devolucoes`);
      setDevolution(data);
    }else if(usuario?.id_perfil === 2){
      const {data} = await http.get(`/devolucoes/${usuario?.id}`);
      setDevolution(data);
    }
  }

  useEffect(()=>{
    getAllDevolution();
  }, [])

  return (
    <Card>
      {devolution && 
        <DevolutionTable devolutions={devolution?devolution:[]} panel={true} className=""/>
      }
      
    </Card>
  );
}

export default Devolution;
