
import styles from '../Modal.module.scss';
import  {AiFillCloseCircle} from 'react-icons/ai'
import { useContext } from 'react';
import { Title } from 'src/components/Title/Title';
import { ModalPropsTestEdit } from 'src/default/interfaces/Interfaces';
import { UserContext } from 'src/context/UserContext';
import {FiDownload} from 'react-icons/fi';
import React from 'react';
import {LoadingButton} from '@mui/lab';
import {TextField} from '@mui/material';
import {Button} from '@mui/material'
import http from 'src/http';
import { IOrder } from 'src/models/product';
import {useEffect} from 'react';
export const DevolutionModal = ({openModal, setModal, data, edit}: ModalPropsTestEdit) => {

    const [status, setStatus] = React.useState();
    const [loading, setLoading] = React.useState<Boolean>(false);
    const [message, setMessage] = React.useState<string>('');
    const [idOrder, setIdOrder] = React.useState<number>(null);

    //data orders
    const [description, setDescription] = React.useState<string>('');
    const [beginDate, setBeginDate] = React.useState('');
    const [endDate, setEndDate] = React.useState('');
    const [idUser, setIdUser] = React.useState(null);
    const [total, setTotal] = React.useState(null);
    const [itens, setItens] = React.useState([]);

  
    const {
      usuario
    } = useContext(UserContext);

  function formatDate(date: Date) {
    var d = new Date(date),
      month = '' + (d.getMonth() + 1),
      day = '' + d.getDate(),
      year = d.getFullYear();
      if (month.length < 2) 
          month = '0' + month;
      if (day.length < 2) 
          day = '0' + day;
    return [day, month, year].join('-');
  }

    const downloadFile = (e)=>{
      e.preventDefault();
      const link = document.createElement('a');
      link.href = `https://nestrental.com.br:80${data[0].anexo}`;
      link.setAttribute(
        'download',
        'devolucao.pdf',
      );

      link.setAttribute('target', '_blank');
      document.body.appendChild(link);
      link.click();
      link.parentNode.removeChild(link);
    }

    const changeStatus = (e) =>{
      setStatus(e.target.value)
    }

    async function editStatusDevolution(event: React.MouseEvent<HTMLButtonElement, MouseEvent>){
      event.preventDefault();
      setLoading(true);
      let formData = new FormData();
      formData.append("status", status);
      formData.append("data_inicio", new Date(data[0].data_inicio).toISOString().slice(0, 19).replace('T', ' '));
      formData.append("data_devolucao", new Date(data[0].data_devolucao).toISOString().slice(0, 19).replace('T', ' '));
      const fetchEditStatusDevolution = fetch(`https://nestrental.com.br:80/api/devolucoes/${data[0].id}`, {
        method: 'PUT',
        body: formData,
        redirect: 'follow'
      })
    
      const dados = await fetchEditStatusDevolution;
      const dataJson = await dados.json();
      if(dataJson){
        setMessage('Status alterado, atualizando...');
        editStatusOrder();
      }else{
        setMessage('Erro no servidor, tente novamente mais tarde.');
      }
     
      setTimeout(()=>{
        window.location.reload();
      }, 2000)
       setLoading(false)
      
    }

    async function editStatusOrder(){
      http.put<IOrder>(`/pedidos/${idOrder}`, {
        status: 'encerrado',
        descricao: description,
        data_inicio: new Date(beginDate).toISOString().slice(0, 19).replace('T', ' '),
        data_entrega: new Date(endDate).toISOString().slice(0, 19).replace('T', ' '),
        vr_total: total,
        itens: itens,
        id_usuario: idUser
      })
      .then(response=>{
        //console.log(response)
      }).catch(err=>{
        console.log(err)
      })
    } 

    useEffect(()=>{
      if(data != null){
        setIdOrder(data[0].pedido?.id);
        setDescription(data[0].pedido?.descricao);
        setBeginDate(data[0].pedido?.data_inicio);
        setEndDate(data[0].pedido?.data_entrega);
        setTotal(data[0].pedido?.itens[0].valor);
        setItens(data[0].pedido?.itens);
        setIdUser(data[0].pedido?.cliente?.id);
      }
    },[data != null])


 
  return (
    <>
      {openModal && (
        <div className={styles.modal} style={{height: '400px'}}>
          <div className={styles.modal__divCloseButton}>
          <button 
              className={styles.modal__divCloseButton__closeButton}
              onClick={(e)=>setModal(!openModal)}
            >
              <AiFillCloseCircle/>
            </button>
          
            <form className={styles.formCheckout} style={{textAlign: 'left'}}>
                <Title level={3}>
                    Detalhes da devolução
                </Title>
                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', gridGap: '1rem', marginTop: '1rem'}}>
                    <div style={{width: '100%'}}>
                      <TextField 
                        id="name_client"
                        type="text"
                        label="Cliente"
                        fullWidth={true}
                        required={true}
                        color="primary"
                        disabled={true}
                        size="small"
                        value={data[0]?.pedido.cliente.razao_social}
                      />
                    </div>
                    <div style={{width: '100%'}}>
                      <TextField 
                          id="email_client"
                          type="text"
                          disabled={true}
                          label="Email"
                          size="small"
                          fullWidth={true}
                          required={true}
                          color="primary"
                          value={data[0]?.pedido.cliente.email}
                      />
                    </div>
                </div>
                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', gridGap: '1rem', marginTop: '1rem'}}>
                    <div style={{width: '100%'}}>
                      <TextField 
                        id="product_client"
                        type="text"
                        disabled={true}
                        label="Produto"
                        fullWidth={true}
                        size="small"
                        required={true}
                        color="primary"
                        value={data[0]?.pedido.itens[0]?.produto.nome}
                      />
                    </div>
                    <div style={{width: '100%'}}>
                      <TextField 
                          id="valor_product"
                          type="text"
                          disabled={true}
                          label="Valor R$"
                          fullWidth={true}
                          required={true}
                          color="primary"
                          size="small"
                          value={data[0]?.pedido.itens[0].valor? 
                            data[0]?.pedido.itens[0].valor.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})
                            : null
                          }
                      />
                    </div>
                </div>

                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', gridGap: '1rem', marginTop: '1rem'}}>
                    <div style={{width: '100%'}}>
                      <TextField 
                          id="date_begin_rent"
                          type="text"
                          label="Início"
                          disabled={true}
                          fullWidth={true}
                          size="small"
                          required={true}
                          color="primary"
                          value={formatDate(data[0]?.data_inicio)}
                        />
                    </div>
                    <div style={{width: '100%'}}>
                      <TextField 
                        id="date_rend_rent"
                        type="text"
                        label="Devolução"
                        fullWidth={true}
                        required={true}
                        size="small"
                        disabled={true}
                        color="primary"
                        value={formatDate(data[0]?.data_devolucao)}
                      />
                    </div>
                </div>

                <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', gridGap: '2rem', marginTop: '.5rem'}}>
                  {usuario?.id_perfil === 1 ||usuario?.id_perfil === 3 ? 
                    <div style={{width: '25%'}}>
                      <label htmlFor="start" style={{display: "block", paddingTop: '1rem'}}>Status da devolução</label>
                      <select
                        onChange={(e)=>changeStatus(e)} 
                        disabled={data[0].status === 'E'? true: false}
                        style={{width: "100%", padding: ".7rem", borderRadius: "8px", border: "1px solid #9099A9"}}>
                        <option value="A" selected={data[0].status === 'A'? false: true}>Pendente</option>
                        <option value="E" selected={data[0].status === 'E'? true: false}>Concluído</option>
                      </select>
                    </div>
                    :
                    <div style={{width: '25%'}}>
                      <label htmlFor="start" style={{display: "block", paddingTop: '.5rem'}}>Status da devolução</label>
                      <input type="text"
                        name="end" 
                        value={data[0]?.status == 'A'? 'Pendente': 'Concluído'}
                        style={{width: "100%", borderRadius: "10px", border: "1px solid #9099A9", padding: '.7rem', color:'#9099A9'}}
                        disabled
                      />
                    </div>
                  }



                  {usuario?.id_perfil === 1 ||usuario?.id_perfil === 3 ?
                  <div style={{width: '100%'}}>
                    <label htmlFor="start" style={{display: "block", paddingTop: '1rem'}}>Documento anexado</label>
                    <Button
                      color="primary"
                      variant="outlined"
                      type="button"
                      size='medium'
                      onClick={(e)=>downloadFile(e)}

                    >
                      Download
                      <FiDownload size={15}/>
                    </Button>
                  </div>
                  :
                  <>
                  </>
                }
              </div>
              {usuario?.id_perfil === 1 ||usuario?.id_perfil === 3 ?
                  <div style={{ display: 'flex', gridGap: '1rem', width: '20rem', marginTop: '1rem'}}>
                    <LoadingButton
                      type="submit"
                      color="primary"
                      variant="contained"
                      onClick={(e)=>editStatusDevolution(e)}
                      disabled={data[0].status === 'A'? false: true}
                    >
                      Atualizar
                    </LoadingButton>
                    <LoadingButton
                      type="button"
                      color="warning"
                      variant="contained"
                      onClick={(e)=>setModal(!openModal)}
                      disabled={data[0].status === 'A'? false: true}
                    >
                      Cancelar
                    </LoadingButton>
                </div> : ''
              }
              {!loading && (
                <p style={{color: 'green'}}>{message}</p>
              )}
                
            </form>
          </div>
          
            
        </div>
      )}
    </>
  )
}
