import { FC, ChangeEvent, useState, useContext, useCallback } from 'react';
import PropTypes from 'prop-types';
import {
  Tooltip,
  Divider,
  Box,
  FormControl,
  Card,
  Checkbox,
  IconButton,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TableContainer,
  Typography,
  useTheme,
  CardHeader
} from '@mui/material';


import { read, utils, writeFileXLSX } from 'xlsx';
import { IDevolution } from 'src/models/product';
import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';
import BulkActions from '../Clients/BulkActions';
import { FaCloudDownloadAlt } from 'react-icons/fa'
import moment from 'moment';
import { BsEye } from 'react-icons/bs'
import { UserContext } from 'src/context/UserContext';
import { DevolutionModal } from 'src/components/Modals/DevolutionModal/DevolutionModal';
import http from 'src/http';



//interfaces
interface DevolutionTableProps {
  className?: string;
  devolutions: IDevolution[];
  panel: boolean;
}



const DevolutionTable: FC<DevolutionTableProps> = ({ devolutions, panel }) => {

  const [selectedDevolutions, setSelectedDevolution] = useState<string[]>(
    []
  );
  const selectedBulkActions = selectedDevolutions.length > 0;
  const [page, setPage] = useState<number>(0);
  const [limit, setLimit] = useState<number>(5);


  const applyFilters = (
    devolutions: IDevolution[],
  ): IDevolution[] => {
    return devolutions.filter((devolution) => {
      let matches = true;

      /*if (filters.status && cryptoOrder.status !== filters.status) {
        matches = false;
      }*/

      return matches;
    });
  };


//pagination
const applyPagination = (
  devolutions: IDevolution[],
  page: number,
  limit: number
): IDevolution[] => {
  return devolutions.slice(page * limit, page * limit + limit);
};

const exportFile = useCallback(() => {
  const ws = utils.json_to_sheet(devolutions);
  const wb = utils.book_new();
  utils.book_append_sheet(wb, ws, "Data");
  writeFileXLSX(wb, "Devolucao.xlsx");
}, [devolutions]);

  const handleSelectAllDevolutions = (
    event: ChangeEvent<HTMLInputElement>
  ): void => {
    setSelectedDevolution(
      event.target.checked
        ? devolutions.map((devolution) => devolution.id)
        : []
    );
  };

  const handleSelectOneDevolution = (
    event: ChangeEvent<HTMLInputElement>,
    devolution: any
  ): void => {
    if (!devolutions.includes(devolution)) {
      setSelectedDevolution((prevSelected) => [
        ...prevSelected,
        devolution
      ]);
    } else {
      setSelectedDevolution((prevSelected) =>
        prevSelected.filter((id) => id !== devolution)
      );
    }
  };

  const handlePageChange = (event: any, newPage: number): void => {
    setPage(newPage);
  };

  const handleLimitChange = (event: ChangeEvent<HTMLInputElement>): void => {
    setLimit(parseInt(event.target.value));
  };

  const filteredDevolutions = applyFilters(devolutions);
  const paginatedDevolutions = applyPagination(
    filteredDevolutions,
    page,
    limit
  );
  const selectedSomeDevolutions =
  selectedDevolutions.length > 0 &&
  selectedDevolutions.length < devolutions.length;
  const selectedAllDevolutions =
  selectedDevolutions.length === devolutions.length;
  const theme = useTheme();

  const [data, setData] = useState<IDevolution[]>([]);
  const [idOrder, setIdOrder] = useState<IDevolution[]>([]);
  const [modal, setModal] = useState<Boolean>(false);
  

  async function queryDevolutionById(idDevolution: any){
    setModal(!modal)
    setData(null)
    const {data} = await http.get(`/devolucoes/devolucao/${idDevolution}`);

    if(data){
      setData(data);
    }else{
      setData(null);
    }
  }

 
  const {
    usuario
  } = useContext(UserContext);



  
  return (
    <Card >
      {selectedBulkActions && (
        <Box flex={1} p={2}>
          <BulkActions />
        </Box>
      )}
      {!selectedBulkActions && (
        <CardHeader
          action={
            <Box width={150}>
              <FormControl fullWidth variant="outlined">
              </FormControl>
              {!panel && (
                <FaCloudDownloadAlt onClick={exportFile} style={{
                textAlign: 'right',
                position: 'relative',
                left: '7rem',
                height: '25px',
                width: '25px',
                cursor: 'pointer'
               }}/>
              )}
            </Box>
            
            
          }
          title={'Devoluções'}
          
        />
      )}
      <Divider />
      <TableContainer>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell >
                
              </TableCell>
              <TableCell>Empresa</TableCell>
              <TableCell>Contato</TableCell>
              <TableCell>Devolução</TableCell>
              <TableCell align="right">Valor</TableCell>
              <TableCell align="right">Status</TableCell>
              <TableCell align="right">Ações</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {paginatedDevolutions.map((devolution) => {
              const isDevolutionSelected = selectedDevolutions.includes(devolution.id);
              return (
                <TableRow
                  hover
                  key={devolution.id}
                  selected={isDevolutionSelected}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      color="primary"
                      checked={isDevolutionSelected}
                      onChange={(event: ChangeEvent<HTMLInputElement>) =>
                        handleSelectOneDevolution(event, devolution.id)
                      }
                      value={isDevolutionSelected}
                    />
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {devolution.pedido.cliente.razao_social}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {devolution.pedido.cliente.email}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >
                      {moment(devolution.data_inicio).utc().format('DD/MM/yyyy')}
                    </Typography>
                    <Typography 
                        variant="body1"
                        fontWeight="bold"
                        color="text.secondary"
                        gutterBottom 
                        noWrap
                    >
                      {moment(devolution.data_devolucao).utc().format('DD/MM/yyyy')}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >

                    
                    </Typography>
                    <Typography variant="body2" color="text.secondary" noWrap>
                      {Number(devolution.pedido.vr_total)?.toLocaleString('pt-br',{style: 'currency', currency: 'BRL'})}
                    </Typography>
                  </TableCell>
                  <TableCell align="right">
                    <Typography
                      variant="body1"
                      fontWeight="bold"
                      color="text.primary"
                      gutterBottom
                      noWrap
                    >

                    {devolution.status == "A" ? <p style={{color: '#EF5B25'}}>Pendente</p> : <p style={{color: 'green'}}>Concluído</p>}
                    </Typography>
                  </TableCell>

                  {usuario?.id_perfil === 2 || usuario?.id_perfil === 3 ? 
                    <TableCell align="right">
                    <Tooltip title="Visualizar pedido" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryDevolutionById(devolution.id)}
                      >
                        <BsEye style={{width: '20px', height: '20px'}}/>
                      </IconButton>
                    </Tooltip>
                  </TableCell>
                  :
                  <TableCell align="right">
                    <Tooltip title="Editar pedido" arrow>
                      <IconButton
                        sx={{
                          '&:hover': {
                            background: theme.colors.primary.lighter
                          },
                          color: theme.palette.primary.main
                        }}
                        color="inherit"
                        size="small"
                        onClick={(e)=>queryDevolutionById(devolution?.id)}
                      >
                        <EditTwoToneIcon fontSize="small" />
                      </IconButton>
                    </Tooltip>
                    {/*<Tooltip title="Excluir pedido" arrow>
                      <IconButton
                        sx={{
                          '&:hover': { background: theme.colors.error.lighter },
                          color: theme.palette.error.main
                        }}
                        color="inherit"
                        size="small"
                      >
                        <DeleteTwoToneIcon fontSize="small" />
                      </IconButton>
                      </Tooltip>*/}
                  </TableCell>
                  }
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Box p={2}>
        <TablePagination
          component="div"
          count={filteredDevolutions.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25, 30]}
        />
      </Box>

      {data  && modal &&(
        <DevolutionModal 
          openModal={modal}
          setModal={setModal}
          data={data}
          edit={true}
        />
      )}
    </Card>
  );
};

DevolutionTable.propTypes = {
  devolutions: PropTypes.array.isRequired
};

DevolutionTable.defaultProps = {
  devolutions: []
};

export default DevolutionTable;
